import { useState, useEffect } from "react";
import createAxiosInstance from "../../config/api";
import { useAuth } from "../../config/AuthContext";
import {useNavigate} from "react-router-dom";

const useWorkDefaultData = () => {
    const { username } = useAuth();
    const navigate = useNavigate();
    const [workDefaultData, setWorkDefaultData] = useState({});
    let cachedHolidays = null; //       

    useEffect(() => {
        if (cachedHolidays) {
            setWorkDefaultData(cachedHolidays); //     
            return;
        }

        const fetchData = async () => {
            try {
                const axiosInstance = createAxiosInstance();
                const response = await axiosInstance.get(
                    '/workSchedule/default'
                );
                cachedHolidays = workDefaultData;
                setWorkDefaultData(response.data);
            } catch (err) {
            }
        };

        fetchData();
    }, [username]);

    useEffect(() => {
        if (workDefaultData.checkInTime === "") {
            window.alert("   . \n   .");
            navigate("/workSchedule/dashBoard");
        }
    }, [workDefaultData]); //  workDefaultData   

    return workDefaultData;
};

export default useWorkDefaultData;
