import React from "react";
import { useNavigate } from "react-router-dom";
import { useState, useEffect } from "react";
import "../../config/index.css";
import createAxiosInstance from "../../config/api";

function PersonnelInsititutionList() {
  const navigate = useNavigate();
  const [data, setData] = useState([]);
  const [error, setError] = useState("");
  const [loading, setLoading] = useState(false);
  const [isFetched, setIsFetched] = useState(false);

  useEffect(() => {
    const fetchData = async () => {
      if (isFetched) return; //    

      setLoading(true); //  

      try {
        const axiosInstance = createAxiosInstance(); //  
        const response = await axiosInstance.get("/personnel/institution/list");
        if (Array.isArray(response.data)) {
          setData(response.data);
          console.log("",response.data);
        } else {
          setError("     .");
        }
        setIsFetched(true); //   
      } catch (error) {
        if (error.response) {
          console.error(
            " :",
            error.response.status,
            error.response.data
          );
          setError("  .");
        } else if (error.request) {
          console.error(" :", error.request);
          setError("  .");
        } else {
          console.error("  :", error.message);
          setError("  .");
        }
      } finally {
        setLoading(false); //  
      }
    };

    fetchData();
  }, [isFetched]);

  const handleRowClick = () => {
    navigate(`/personnel/institution/new`);
  };

  const handleRowClick2 = () => {
    navigate(`/personnel/institution/schedule/list`);
  };

  const handleEmployeeClick = (Id) => {
    navigate(`/personnel/institution/${Id}`); //   
  };

  return (
    <div className="
    container d-flex flex-column justify-content-center align-items-center vh-100
    ">
      <h1 className="title">WEAVUS  </h1>
      <div>
        <button className="submit-button" onClick={() => handleRowClick()}>
            
        </button>
        <button className="submit-button" onClick={() => handleRowClick2()}>
           
        </button>
      </div>
      {loading && <div className="loading">Loading...</div>}
      {error && <div className="error-message">{error}</div>}
      {!loading && !error && (
          <div className="d-flex justify-content-center">
            <table className="table mx-auto text-center">
            <thead>
              <tr>
                <th className="table-header"></th>
                <th className="table-header"> </th>
                <th className="table-header"></th>
                <th className="table-header"> </th>
                <th className="table-header"> </th>
              </tr>
              </thead>
              <tbody>
              {data.map((institution, index) => (
                  <tr key={institution.id}>
                    <td className="table-data">
                      {index + 1}
                    </td>
                    <td className="table-data">
                      <button
                          className="action-button"
                          onClick={() => handleEmployeeClick(institution.id)} //   handleEmployeeClick  
                      >
                        {institution.name}
                      </button>
                    </td>
                    <td className="table-data">
                      {institution.managerName}
                    </td>
                    <td className="table-data">
                      {institution.applicants.length} 
                    </td>
                    <td className="table-data">
                      {
                        // admissionStatus    
                        institution.applicants.filter((applicant) => applicant.admissionStatus !== "").length
                      } 
                    </td>
                  </tr>
              ))}
              </tbody>
            </table>
          </div>
      )}
    </div>
  );
}

export default PersonnelInsititutionList;
