import React from "react";
import { useNavigate } from "react-router-dom";
import { useState, useEffect } from "react";
import "../../config/index.css";
import createAxiosInstance from "../../config/api";
import {Dropdown} from "react-bootstrap";

function PersonnelDashboard() {
  const navigate = useNavigate();
  const [data, setData] = useState([]);
  const [error, setError] = useState("");
  const [loading, setLoading] = useState(false);
  const [isFetched, setIsFetched] = useState(false);
  const [showAll, setShowAll] = useState(false); //  false ( )

  useEffect(() => {
    const fetchData = async () => {
      if (isFetched) return; //    

      setLoading(true); //  

      try {
        const axiosInstance = createAxiosInstance(); //  
        const response = await axiosInstance.get("/personnel/applicant/list");
        if (Array.isArray(response.data)) {
          setData(response.data);
        } else {
          setError("     .");
        }
        setIsFetched(true); //   
      } catch (error) {
        if (error.response) {
          console.error(
            " :",
            error.response.status,
            error.response.data
          );
          setError("  .");
        } else if (error.request) {
          console.error(" :", error.request);
          setError("  .");
        } else {
          console.error("  :", error.message);
          setError("  .");
        }
      } finally {
        setLoading(false); //  
      }
    };

    fetchData();
  }, [isFetched]);

  const handleRowClick = () => {
    navigate(`/personnel/applicant/new`);
  };

  const handleRowClick2 = () => {
    navigate(`/personnel/institution/list`);
  };

  const handleEmployeeClick = (Id) => {
    navigate(`/personnel/applicant/${Id}`); //   
  };
//

  return (
    <div className="container">
      <h1 className="title">WEAVUS  </h1>
      <div className="row">
        <Dropdown className="dropdown col">
          <Dropdown.Toggle variant="success" id="dropdown-basic">
            
          </Dropdown.Toggle>
          <Dropdown.Menu>
            <Dropdown.Item  onClick={() => setShowAll(false)}> </Dropdown.Item>
            <Dropdown.Item onClick={() => setShowAll(true)}> </Dropdown.Item>
            {/*<Dropdown.Item value="hideAll"> </Dropdown.Item>*/}
          </Dropdown.Menu>
        </Dropdown>

        <button className="submit-button col" onClick={() => handleRowClick()}>
           
        </button>
        <button className="submit-button col" onClick={() => handleRowClick2()}>
            
        </button>
      </div>

      {loading && <div className="loading">Loading...</div>}
      {error && <div className="error-message">{error}</div>}
      {!loading && !error && (
        <table className="table">
          <thead>
            <tr>
              <th className="table-header"></th>
              <th className="table-header"></th>
              <th className="table-header"></th>
              <th className="table-header"> </th>
              <th className="table-header"> </th>
            </tr>
          </thead>
          <tbody>
          {data
              .filter(applicant => showAll || applicant.admissionStatus !== "") //  
              .map((applicant) => (
              <tr key={applicant.id}>
                <td className="table-data">
                  <button
                    className="action-button"
                    style={{
                      backgroundColor: applicant.admissionStatus === ""
                          ? "#BDBDBD"
                          : applicant.gender === ""
                              ? "#64B5F6"
                              : "#E57373"
                    }}
                    onClick={() => handleEmployeeClick(applicant.id)} //   handleEmployeeClick  
                  >
                    {applicant.name}
                  </button>
                </td>
                <td className="table-data">
                  {applicant.gender}
                </td>
                <td className="table-data">
                  {applicant.institution.name}
                </td>
                <td className="table-data">
                  {applicant.admissionStatus}
                </td>
                <td className="table-data">
                  D +{applicant.statusDate}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      )}
    </div>
  );
}

export default PersonnelDashboard;
