import React from "react";
import { useNavigate } from "react-router-dom";
import {
  getStateText,
  getEmployeeTypeText,
  formatAmount,
} from "../../utils/textUtils";
import "../../config/index.css";

import { useFetchAccual } from "../utils/useFetchAccual"; //    

function Dashboard() {
  const navigate = useNavigate();
  const { userData, userError, userLoading } = useFetchAccual(); //   

  const handleEmployeeClick = (employeeId) => {
    navigate(`/dashboard/${employeeId}`); //   
  };

  //         
  if (userLoading) {
    return <div className="loading">Loading...</div>;
  }

  if (userError) {
    return <div className="error">{userError}</div>;
  }

  return (
    <div className="container">
      <h1 className="title">WEAVUS  </h1>
      <div className="table-responsive">
        <table className="table table-responsive">
          <thead>
          <tr>
            <th className="text-center"></th>
            <th className="text-center"> </th>
            <th className="text-center"></th>
            <th className="text-center"></th>
            <th className="text-center"> </th>
            <th className="text-center"> </th>
          </tr>
          </thead>
          <tbody>
          {userData.map((item) => (
              <tr key={item.id}>
                <td className="table-data">
                  <button
                      className="action-button"
                      onClick={() => handleEmployeeClick(item.employee.id)}
                  >
                    {item.employee.name}
                  </button>
                </td>
                <td className="table-data">
                  {getEmployeeTypeText(
                      item.employee.employeeType,
                      item.employee.status,
                      item.employee.exitDate
                  )}
                </td>
                <td className="table-data ">{item.employee.entryDate}</td>
                <td className="table-data ">
                  {item.employee.exitDate ? item.employee.exitDate : ""}
                </td>
                <td className="table-data">
                  {formatAmount(item.totalAmount ? item.totalAmount : "0")}
                </td>
                <td className="table-data ">{getStateText(item.state)}</td>
              </tr>
          ))}
          </tbody>
        </table>
      </div>
    </div>
  );
}

export default Dashboard;
