import React, { useEffect, useState } from "react";
import "../../config/index.css";
import { getRankText, formatAmount } from "../../utils/textUtils";
import createAxiosInstance from "../../config/api";

const EmployeeSettings = () => {
  const [employees, setEmployees] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [password2, setPassword2] = useState("");
  // const [username, setUsername] = useState("");
  // const [password, setPassword] = useState("");
  const [item, setItem] = useState({
        username : "",
        password : "",
        role : "",
  });

  useEffect(() => {
    const fetchEmployee = async () => {
      try {
        const axiosInstance = createAxiosInstance(); //  
        const response = await axiosInstance.get("/employees/admin/setting");
        setEmployees(response.data);
        setLoading(false);
      } catch (err) {
        setError("   .");
        setLoading(false);
        console.error(err); //  
      }
    };
    fetchEmployee();
  }, []);

  const handleAmountChange = (index, value) => {
    const updatedEmployees = [...employees];
    const numericValue = value.replace(/[^0-9]/g, "");
    updatedEmployees[index].monthlyAmount = Number(numericValue);
    setEmployees(updatedEmployees);
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setItem((prevItem) => ({
      ...prevItem,
      [name]: value,
    }));
  };
  const handlePassword2Change = (e) => {
    setPassword2(e.target.value);
  };

  const handleSave = async () => {
    try {
      const axiosInstance = createAxiosInstance(); //  
      await axiosInstance.put("/employees/admin/setting", employees);
      alert("  .");
    } catch (err) {
      alert("   . " + err.message);
    }
  };

  //     
  const adminSignUp = async (e) => {
    e.preventDefault(); //   
    if (item.password !== password2) {
        alert("  .");
        return;
    }
    const confirmSave = window.confirm("   ?");
    if (!confirmSave) {
      return;
    } else {
      try {
        const axiosInstance = createAxiosInstance(); //  
        const response2 = await axiosInstance.post("/signup",
            item );
        const {message} = response2.data;
        window.alert(message);
        setItem({
          username : "",
          password : "",
          role : "",
        }); // 
        setPassword2(""); // 
      } catch (err) {
        alert("  . ");
      }
    }
  }


  if (loading) return <div className="loading">Loading...</div>;
  if (error) return <div className="error">{error}</div>;


  return (
    <div className="container d-flex justify-content-center align-items-center flex-column">
      <h1 className="title">WEAVUS  </h1>
      <div className="row justify-content-md-center">
        <div className="col-md-14">
          <table className="table ">
            <thead>
            <tr>
              <th className="table-header"></th>
              <th className="table-header">   </th>
            </tr>
            </thead>
            <tbody>
            {employees.map((employee, index) => (
                <tr key={index}>
                  <td className="table-data ">{getRankText(employee.rank)}</td>
                  <td className="table-data ">
                    <input
                        type="text"
                        className="input"
                        value={formatAmount(employee.monthlyAmount)}
                        onChange={(e) => handleAmountChange(index, e.target.value)}
                    />
                  </td>
                </tr>
            ))}
            </tbody>
          </table>
          <button className="save-button" onClick={handleSave}>
             {""}
            
          </button>
          <form onSubmit={adminSignUp}>
            <h2> </h2>
            <label htmlFor="username" className="label">
               :
            </label>
            <input
                name="username"
                type="text"
                value={item.username}
                onChange={handleInputChange}
                placeholder=""
                className="input"
                required
            />
            <label htmlFor="password" className="label">
               :
            </label>
            <input
                name="password"
                type="password"
                value={item.password}
                onChange={handleInputChange}
                placeholder=""
                className="input"
                required
            />
            <label htmlFor="password" className="label">
               :
            </label>
            <input
                name="password2"
                type="password"
                value={password2}
                placeholder=" "
                className="input"
                onChange={handlePassword2Change}
                required
            />
            <label htmlFor="password" className="label">
                :
            </label>
            <select
                name="role"
                type=""
                value={item.role}
                onChange={handleInputChange}
                className="input"
                required
            >
              <option value=""> </option>
              <option value="ADMIN"></option>
              <option value="GENERAL"></option>
            </select>
            <button type="submit" className="btn btn-success"></button>
          </form>
        </div>
      </div>
    </div>
  );
}

export default EmployeeSettings;
