import React, {useEffect, useState} from "react";
import {useLocation, useNavigate, useParams} from "react-router-dom";
import {useAuth} from "../../config/AuthContext";
import useWorkData from "../utils/WorkData";
import createAxiosInstance from "../../config/api";
import workDataDefault from "../utils/WorkDataDefault";

function WorkScheduleDashboard (){
    const { username } = useAuth();
    const { date } = useParams();
    const year = new Date(date).getFullYear();
    const month = new Date(date).getMonth()+ 1;
    const navigate = useNavigate();
    const [item, setItem] = useState({});
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState("")
    const [isEditing, setIsEditing] = useState(false);
    const [editedItem, setEditedItem] = useState({});
    const [uploading, setUploading] = useState(false);
    const [selectedFile, setSelectedFile] = useState(null);
    const [progress, setProgress] = useState(0); //   
    const [fileStatus, setFileStatus ] = useState(false);

    const workData = useWorkData(year, month); //   
    const data = workDataDefault();
    const [defaultItem] = useState({
        id: "",
        checkInDate: date,
        checkInTime: "",
        checkOutDate: date,
        checkOutTime: "",
        breakTimeIn: "",
        breakTimeOut: "",
        workType: "",
        workPosition: "",
        workLocation: "",
        memo: ""
        });

    useEffect(() => {
                if (workData.workData[date]) {
                    setItem(workData.workData[date]);
                    setEditedItem(workData.workData[date]);
                    console.log ("item  : " ,item)
                } else {
                    const newDefaultItem = {
                        ...defaultItem,
                        checkInTime: data.checkInTime,
                        checkOutTime: data.checkOutTime,
                        breakTimeIn: data.breakTimeIn,
                        breakTimeOut: data.breakTimeOut,
                        workLocation: data.workLocation,
                    };
                    setItem(null);
                    setEditedItem(newDefaultItem);
                }
        }, [data, workData?.workData]);

    useEffect(() => {
        // workData    isEditing true 
        if (!workData.workData[date] || workData.workData[date].isEmpty) {
            setIsEditing(true);
        } else {
            setIsEditing(false);  //     false 
        }
    }, [workData?.workData]);  // workData   

    function handleClickBack() {
        navigate("/workSchedule/list");
    }
    const handleEditClick = () => {
        setIsEditing(true);
    };
    function handleCancelClick() {
        if(!item || item.isEmpty){
            navigate("/workSchedule/list");
        } else {
            setIsEditing(false);
            setEditedItem(item);
        }
    }
    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setEditedItem((prevItem) => ({
            ...prevItem,
            [name]: value,
        }));
    };

    const handleSubmit = async (e) => {
        e.preventDefault(); //   
        const confirmSave = window.confirm("   ?");
        if (!confirmSave) {
            return;
        } else {
            setLoading(true);
            setError("");
            try {
                // console.log("",editedItem);
                if(editedItem.workType !== ""){
                    editedItem.workPosition = "";
                }
                if(editedItem.workType === ""){
                    editedItem.memo = "-";
                }
                const axiosInstance = createAxiosInstance(); //  
                await axiosInstance.post(
                    "/workSchedule/save",
                    editedItem
                );
                setItem(editedItem);
                setEditedItem(editedItem);
                setIsEditing(false);
                window.alert("  ");
                navigate("/workSchedule/list")
            } catch (err) {
                setError(err.response?.status === 404 ? "     " : "    .");
            }finally {
                setLoading(false);
            }
        }
    };

    const handleDeleteClick = async (e) =>{
        e.preventDefault();
        const confirmDelete = window.confirm("   ?");
        if (!confirmDelete) {
            return;
        } else {
            setLoading(true);
            setError("");
            try{
                const axiosInstance = createAxiosInstance(); //  
                await axiosInstance.delete(`/workSchedule/delete/${editedItem.id}`);
                window.alert("  ");
                navigate("/workSchedule/list");
            }catch (err){

            } finally {
                setLoading(false);
            }
        }
    };

    const handleFileChange = (e) => {
        const file = e.target.files[0];

        if (!file) return;

        const allowedExtensions = ["jpg", "jpeg", "png"];
        const fileExtension = file.name.split(".").pop().toLowerCase();

        if (!allowedExtensions.includes(fileExtension)) {
            alert("JPG  PNG    .");
            e.target.value = ""; //   
            return;
        }
        setSelectedFile(file);
    };

    if (loading) return <div> ...</div>;
    if (error) return <div>{error}</div>;

    const handleClickDelete = async (id, name) => {
        const confirmDelete = window.confirm(`_${name.split("_").pop()}() ?`);
        if (!confirmDelete) return;
        try {
            const axiosInstance = createAxiosInstance();
            await axiosInstance.delete(`/workSchedule/file/delete/${id}`);
            window.alert(" .");
            navigate("/workSchedule/list");
        } catch (error) {
            console.error("  :", error);
            alert("    . \n   .");
        }
    }

        const handleUpload = async () => {
            if(selectedFile === null) {
                window.alert(" .");
                return;
            }
            if(item?.fileId) {
                const confirmDelete = window.confirm("   .  ?");
                if (!confirmDelete) return;
            }
            setUploading(true);
            setProgress(0); // 
            try {
                const formData = new FormData();
                formData.append("file", selectedFile);
                formData.append("date", date);
                formData.append("type", 0);
                const axiosInstance = createAxiosInstance();
                await axiosInstance.post("/workSchedule/file/upload",formData,{
                    headers: {
                        "Content-Type": "multipart/form-data",
                    },
                    onUploadProgress: (progressEvent) => {
                        const percentCompleted = Math.round(
                            (progressEvent.loaded * 100) / progressEvent.total
                        );
                        setProgress(percentCompleted);
                    },
                });
                setFileStatus(true);
                window.alert("  !");
                localStorage.setItem("fileStatus", JSON.stringify(true));
                localStorage.setItem("selectedFile", JSON.stringify(selectedFile));
            } catch (error) {
                if (error.response && error.response.status === 400) {
                    window.alert(error.response.data);
                    setFileStatus(false);
                } else {
                    window.alert("    .");
                    setFileStatus(false);
                }
            }finally {
                setProgress(0);
                setUploading(false);
            }
        };


    return (
        <div className="container d-flex">
            <div className="card">
                <form onSubmit={handleSubmit}>
                <div className="card-header">
                    <h3>  </h3>
                </div>
                <div className="card-body">
                    {isEditing ? (
                        <>
                            <div>
                                <input
                                    className="form-control mb-2"
                                    name="id"
                                    value={editedItem?.id}
                                    onChange={handleInputChange}
                                    disabled
                                />
                            </div>
                            <div className="form-group">
                                <label className="label">  / </label>
                                <div className="d-flex">
                                    <input
                                        name="checkInDate"
                                        type="date"
                                        className="input"
                                        value={editedItem.checkInDate}
                                        onChange={handleInputChange}

                                    />
                                    <input
                                        name="checkInTime"
                                        type="time"
                                        className="input"
                                        value={editedItem.checkInTime}
                                        onChange={handleInputChange}

                                    />
                                </div>
                            </div>
                            <div className="form-group">
                                <label className="label">  / </label>
                                <div className="d-flex">
                                    <input
                                        name="checkOutDate"
                                        type="date"
                                        className="input"
                                        value={editedItem.checkOutDate}
                                        onChange={handleInputChange}
                                    />
                                    <input
                                        name="checkOutTime"
                                        type="time"
                                        className="input"
                                        value={editedItem.checkOutTime}
                                        onChange={handleInputChange}
                                    />
                                </div>
                            </div>
                            <div className="form-group">
                                <label className="label"></label>
                                <div className="d-flex">
                                    <input
                                        name="breakTimeIn"
                                        type="time"
                                        className="input"
                                        value={editedItem.breakTimeIn}
                                        onChange={handleInputChange}
                                    />
                                    <span> ~ </span>
                                    <input
                                        name="breakTimeOut"
                                        type="time"
                                        className="input"
                                        value={editedItem.breakTimeOut}
                                        onChange={handleInputChange}
                                    />
                                </div>
                            </div>
                            <div className="form-group">
                                <label className="label"> </label>
                                <select
                                    className="form-control"
                                    name="workType"
                                    value={editedItem.workType}
                                    onChange={handleInputChange}
                                    required
                                >
                                    <option value="" disabled>
                                            .
                                    </option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                </select>
                            </div>
                            {editedItem.workType !== "" ? (
                                <div className="form-group">
                                    <label></label>
                                    <input
                                        name="memo"
                                        type="text"
                                        className="input"
                                        placeholder="  ."
                                        value={editedItem.memo}
                                        onChange={handleInputChange}
                                    />
                                </div>
                            ) : (
                                    <>
                                        <input
                                            name="memo"
                                            type="text"
                                            className="input"
                                            value={editedItem.memo? editedItem.memo : " "}
                                            onChange={handleInputChange}
                                            hidden
                                            disabled
                                        />
                                    </>
                            )}
                                    {editedItem.workType !== "" ? (
                                        <></>
                                            ) : (
                                        <div className="form-group">
                                            <label className="label"> </label>
                                            <select
                                                name="workPosition"
                                                className="form-control"
                                                value={editedItem.workPosition}
                                                onChange={handleInputChange}
                                                required
                                            >
                                                <><option value="" disabled>
                                                        .
                                                </option>
                                                    <option value=""></option>
                                                    <option value=""></option>
                                                    <option value=""></option>
                                                </>
                                            </select>
                                        </div>
                                    )}
                            <div className="form-group">
                                <label className="label"></label>
                                <input
                                    name="workLocation"
                                    type="text"
                                    className="input"
                                    value={editedItem.workLocation}
                                    onChange={handleInputChange}
                                    disabled
                                />
                            </div>
                            {editedItem.checkInTime !== data.checkInTime ? ( //    
                                <>
                                    <label className="label">  </label>
                                    <div className="row-cols-1">
                                        {uploading && (
                                            <div style={{ marginBottom: "10px" }}>
                                                <p>  : {progress}%</p>
                                                <div
                                                    style={{
                                                        // width: "100%",
                                                        backgroundColor: "#e0e0e0",
                                                        borderRadius: "5px",
                                                    }}
                                                >
                                                    <div
                                                        style={{
                                                            width: `${progress}%`,
                                                            backgroundColor: "#4caf50",
                                                            height: "10px",
                                                            borderRadius: "5px",
                                                        }}
                                                    ></div>
                                                </div>
                                            </div>
                                        )}
                                        <div className="d-flex align-items-center gap-2 mb-2 my-2 justify-content-start">
                                            <strong>
                                                <label className="label">.jpg, .png  </label>
                                            </strong>
                                        </div>
                                        <div className="d-flex gap-2 mb-3" style={{ width: '80%' }}>
                                            <div className="input-group">
                                                <input
                                                    type="file"
                                                    className="form-control"
                                                    accept="image/jpeg, image/png"
                                                    onChange={handleFileChange}
                                                    placeholder=" "
                                                    style={{ width: '70%' }}
                                                />
                                                <button onClick={handleUpload}
                                                        className="btn btn-primary"
                                                        type="button">
                                                    
                                                </button>
                                            </div>
                                        </div>
                                    </div>
                                </>

                            ) : null}
                        </>
                        ) : (
                            <>
                                <div className="form-group">
                                    <label className="label"> </label>
                                    <div className="d-flex">
                                        <input
                                            type="time"
                                            className="input"
                                            value={item?.checkInTime || defaultItem.checkInTime}
                                            readOnly
                                        />
                                        <span> ~ </span>
                                        <input
                                            type="time"
                                            className="input"
                                            value={item?.checkOutTime || defaultItem.checkOutTime}
                                            readOnly
                                        />
                                    </div>
                                </div>
                                <div className="form-group">
                                    <label className="label"></label>
                                    <div className="d-flex">
                                        <input
                                            type="time"
                                            className="input"
                                            value={item?.breakTimeIn || defaultItem.breakTimeIn}
                                            readOnly
                                        />
                                        <span className="text-gray-500 me-2 fs-5"> ~ </span>
                                        <input
                                            type="time"
                                            className="input"
                                            value={item?.breakTimeOut|| defaultItem.breakTimeOut}
                                            readOnly
                                        />
                                    </div>
                                </div>
                                <div className="form-group">
                                    <label className="label"> </label>
                                    <input
                                        type="text"
                                        className="input"
                                        value={item?.workType|| defaultItem.workType}
                                        readOnly
                                    />
                                </div>
                                <div className="form-group">
                                    <label className="label"> </label>
                                    <input
                                        type="text"
                                        className="input"
                                        value={item?.workPosition|| defaultItem.workPosition}
                                        readOnly
                                    />
                                </div>
                                <div className="form-group">
                                    <label className="label"></label>
                                    <input
                                        type="text"
                                        className="input"
                                        value={item?.workLocation|| defaultItem.workLocation}
                                        readOnly
                                    />
                                </div>
                                <div className="form-group">
                                    <label></label>
                                    <input
                                            type="text"
                                            className="input"
                                            value={item?.memo|| defaultItem.checkInTime}
                                            readOnly
                                    />
                                </div>
                                {item?.fileId ? (
                                    <div className="form-group">
                                        <label>  </label>
                                        <span>
                                            <a href={item.fileUrl} target="_blank" rel="noreferrer">
                                                {item.fileName.split("_").pop()}
                                            </a>
                                            &nbsp;
                                            <i className="bi bi-trash-fill"
                                               onClick={() => handleClickDelete(
                                                   item.fileId, item.fileName
                                               )}></i>
                                        </span>
                                    </div>
                                    ) :
                                     (<>
                                         {item.checkInTime !== data.checkInTime ? ( //    
                                             <label>  </label>
                                         ) : null}
                                    </>)
                                }
                            </>
                    )}
                </div>
                <div className="card-footer">
                    {isEditing ? (
                        <button
                            type="button"
                            className="btn btn-primary me-4"
                            onClick={handleCancelClick}
                        >
                            
                        </button>
                    ) : (
                        <button
                            type="button"
                            className="btn btn-primary me-3"
                            onClick={handleClickBack}
                        >
                             
                        </button>
                    )}

                    {isEditing ? (
                        <button
                            type="submit"
                            className="btn btn-secondary"
                        >
                            
                        </button>
                    ) : (<></>)}

                    {isEditing ? (<></>) : (
                        <button
                            type="button"
                            className="btn btn-secondary me-4"
                            onClick={handleEditClick}
                        >
                            
                        </button>
                    )}
                    {item && !isEditing ? (<button
                        type="button"
                        className="btn btn-danger "
                        onClick={handleDeleteClick}
                    >
                        
                    </button>
                    ) : (<></>)}
                </div>
                </form>
            </div>
</div>
    );
}
export default WorkScheduleDashboard;