import { gapi } from "gapi-script";

const API_KEY = "AIzaSyBuoYwHGk4XLCfalv3_N5PLDG8gq5kc74U"; // Google API 
const CALENDAR_ID = "ko.japanese.official#holiday@group.v.calendar.google.com"; //    ID

let cachedHolidays = null; //       

const holidayListData = async () => {
    if (cachedHolidays) return cachedHolidays; //     

    try {
        await new Promise((resolve) => gapi.load("client", resolve));
        await gapi.client.init({ apiKey: API_KEY });

        const response = await gapi.client.request({
            path: `https://www.googleapis.com/calendar/v3/calendars/${encodeURIComponent(CALENDAR_ID)}/events`,
        });

        const formattedHolidays = {};
        response.result.items.forEach((event) => {
            const date = new Date(event.start.date);
            const key = `${date.getFullYear()}-${String(date.getMonth() + 1).padStart(2, '0')}-${String(date.getDate()).padStart(2, '0')}`;
            formattedHolidays[key] = event.summary;
        });

        cachedHolidays = formattedHolidays; //    
        return formattedHolidays;
    } catch (err) {
        console.error("Error fetching holidays", err);
        return {};
    }
};

export default holidayListData;
