import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import "../../config/index.css";
import createAxiosInstance from "../../config/api";

import {
  getRankText,
  getEmployeeTypeText,
  getStateText,
  formatAmount,
} from "../../utils/textUtils";

const EmployeeDetailPage = () => {
  const { employeeId } = useParams(); // URL employeeId 
  const [item, setItem] = useState(null); //   
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");

  useEffect(() => {
    //     
    const fetchEmployee = async () => {
      try {
        const axiosInstance = createAxiosInstance(); //  
        const response = await axiosInstance.get(
          `/employees/${employeeId}/accrual`
        );
        setItem(response.data);
      } catch (err) {
        setError("   .");
        console.error(err);
      } finally {
        setLoading(false);
      }
    };

    fetchEmployee();
  }, [employeeId]);

  if (loading) return <loading className="loading">Loading...</loading>;
  if (error) return <error className="error">{error}</error>;

  return (
    <div className="container ">
      <div className="title">
        <h2>{item.employee.name}   </h2>
      </div>
      <div className="row justify-content-md-center">
        <div className="col-md-6">
          <div  className="card">
            {item ? (
                <div className="card-body">
                  <div className="form-group">
                    <label className="label"></label>
                    <p className="box">{item.employee.name}</p>
                  </div>
                  <div className="form-group">
                    <label className="label"> </label>
                    <p className="box">
                      {getEmployeeTypeText(
                          item.employee.employeeType,
                          item.employee.status
                      )}
                    </p>
                  </div>
                  <div className="form-group">
                    <label className="label"></label>
                    <p className="box">{getRankText(item.employee.rank)}</p>
                  </div>
                  <div className="form-group">
                    <label className="label"> </label>
                    <p className="box">{getStateText(item.state)}</p>
                  </div>
                  <div className="form-group">
                    <label className="label">  </label>
                    <p className="box">{item.employee.conversionDate}</p>
                  </div>
                  <div className="form-group">
                    <label className="label"> </label>
                    <p className="box">
                      {formatAmount(item.totalAmount ? item.totalAmount : "0")}
                    </p>
                  </div>
                  <div className="form-group">
                    <label className="label"></label>
                    <p className="box">
                      {item.employee.exitDate ? item.employee.exitDate : ""}
                    </p>
                  </div>
                </div>
            ) : (
                <p>    .</p>
            )}
          </div>
        </div>
      </div>
      </div>
  );
};

export default EmployeeDetailPage;

