import React, {useEffect, useState} from 'react';
import createAxiosInstance from "../../config/api";
import {data, useNavigate, useParams} from "react-router-dom";

const ReceiptList = () => {
    const { month } = useParams();
    const navigate = useNavigate();
    const [receipts, setReceipts] = useState([]);  //  
    const [newReceipt, setNewReceipt] = useState([]);  //   
    const [isAdding, setIsAdding] = useState(false);  //      
    const [progress, setProgress] = useState(0); //   
    const [uploading, setUploading] = useState(false);
    const [selectedFile, setSelectedFile] = useState(null);
    const [fileStatus, setFileStatus ] = useState(false);
    console.log("1",newReceipt)

    useEffect(() => {
        fetchReceipts();
    }, [month]);

    const fetchReceipts = async () => {
        try {
            // const id = month;
            const axiosInstance = createAxiosInstance();
            const response = await axiosInstance.get(`/workSchedule/receipts/${month}`);  // API  
            setReceipts(response.data);  //     
            // console.log('', response.data);
        } catch (error) {
            console.error('    :', error);
        }
    };

    const handleChange = (e) => {
        const { name, value } = e.target;
        setNewReceipt((prev) => ({
            ...prev,
            [name]: value,
        }));
    }

    const handleAddReceipt = async (e) => {
        e.preventDefault();
        if(selectedFile === null) {
            window.alert(" .");
            return;
        }
        setUploading(true);
        setProgress(0); // 
            try {
                const formData = new FormData();
                formData.append("file", selectedFile);
                formData.append("date", newReceipt.receiptDate);
                formData.append("type", 1);
                formData.append(
                    "employeeReceiptData",
                    new Blob([JSON.stringify(newReceipt)], { type: "application/json" })
                );
                const axiosInstance = createAxiosInstance();
                await axiosInstance.post("/workSchedule/receipts", formData, {
                    headers: {
                        "Content-Type": "multipart/form-data",
                    },
                    onUploadProgress: (progressEvent) => {
                        const percentCompleted = Math.round(
                            (progressEvent.loaded * 100) / progressEvent.total
                        );
                        setProgress(percentCompleted);
                    },
                });  // API  
                window.alert('  .');
                setNewReceipt([]);
                setIsAdding(false);  //     
                fetchReceipts();  //     
            }catch (e) {
                window.alert('    :', e);
            }finally {
                setProgress(0);
                setUploading(false);
            }
    };

    function handleClickList() {
        navigate("/workSchedule/list");
    }

    const handleClickEdit = async (id, name) => {
        const confirmSave = window.confirm(`${name}` + "    ?");
        if (!confirmSave) {
            return;
        } else {
            try{
                const axiosInstance = createAxiosInstance();
                const response = await axiosInstance.delete(`/workSchedule/receipts/${id}`);
                window.alert(' .');
                fetchReceipts();
                console.log("",response);
            }catch (e){
                console.error('  :', e);
            }
        }
    }

    const handleFileChange = (e) => {
        setSelectedFile(e.target.files[0]);
    };

    return (
        <div className="container">
            <form onSubmit={handleAddReceipt}>
                <h2 className="text-dark">{month}   /  : {receipts.length}</h2>
                <button className="btn btn-primary " type="button" onClick={handleClickList}>
                    
                </button>
                {receipts.length === 0 ? (
                    <>
                        <p>  </p>
                    </>
                ) : (
                    <ul>
                        <div className="table-responsive">
                            <table className="table table-striped">
                                <thead>
                                <tr>
                                    <th className="text-center"></th>
                                    <th className="text-center"></th>
                                    <th className="text-center"></th>
                                    <th className="text-center"></th>
                                    <th className="text-center"></th>
                                    <th className="text-center"></th>
                                    <th className="text-center"></th>
                                    <th className="text-center"></th>
                                </tr>
                                </thead>
                                <tbody>
                                {receipts.map((receipt, index) => (
                                    <tr key={index}>
                                        <td>
                                            <i class="bi bi-trash-fill"
                                               onClick={() => handleClickEdit(
                                                   receipt.id, receipt.receiptName
                                               )}
                                            >
                                            </i> &nbsp;
                                            {receipt.receiptDate}
                                        </td>
                                        <td>{receipt.receiptType}</td>
                                        <td>{receipt.receiptName}</td>
                                        <td>{receipt.receiptItem}</td>
                                        <td>{receipt.receiptContent}</td>
                                        <td>{receipt.receiptAmount}</td>
                                        <td>{receipt.receiptStatus == 0 ? "":""}</td>
                                        <td>
                                            {receipt.fileId != null ? (
                                                <>
                                                {/*https://drive.google.com/file/d/1XGohMxZcXQaIcQxiDIivDTT-_q7HiPCH/view?usp=drive_link*/}
                                                    <a href={`https://drive.google.com/file/d/${receipt.fileId}/view?usp=drive_link`}
                                                       target="_blank"
                                                       rel="noopener noreferrer">
                                                        <i className="bi bi-file-earmark-check-fill"></i>
                                                    </a>
                                                </>
                                            ) : (null)}
                                        </td>
                                    </tr>
                                ))}
                                </tbody>
                            </table>
                        </div>

                    </ul>
                )}
                <h2>
                    <i className="bi bi-plus-square-dotted" onClick={() => setIsAdding(true)}></i>
                </h2>
                {isAdding && (
                    <ul>
                    <div className="table-responsive">
                        <table className="table table-striped">
                            <thead>
                            <tr>
                                <th className="text-center"></th>
                                <th className="text-center"></th>
                                <th className="text-center"></th>
                                <th className="text-center"></th>
                                <th className="text-center"></th>
                                <th className="text-center"></th>
                                <th className="text-center"></th>
                            </tr>
                            </thead>
                            <tbody>
                            <tr>
                                <td><input
                                    type="date"
                                    value={newReceipt.receiptDate}
                                    className="input"
                                    name="receiptDate"
                                    onChange={handleChange}
                                    placeholder="  "
                                    required
                                /></td>
                                <td><select
                                    value={newReceipt.receiptType}
                                    className="input me-5"
                                    name="receiptType"
                                    onChange={handleChange}
                                    required
                                >
                                    <option value="">, </option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    
                                </select></td>
                                <td><input
                                    type="text"
                                    className="input"
                                    value={newReceipt.receiptName}
                                    name="receiptName"
                                    onChange={handleChange}
                                    placeholder=""
                                    required
                                /></td>
                                <td><select
                                    value={newReceipt.receiptItem}
                                    className="input me-5"
                                    name="receiptItem"
                                    onChange={handleChange}
                                    required
                                >
                                    <option value=""> </option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                </select></td>
                                <td><input
                                    type="text"
                                    value={newReceipt.receiptContent}
                                    className="input"
                                    name="receiptContent"
                                    onChange={handleChange}
                                    placeholder=" "
                                    required
                                /></td>
                                <td><input
                                    type="text"
                                    value={newReceipt.receiptAmount}
                                    className="input"
                                    name="receiptAmount"
                                    onChange={handleChange}
                                    placeholder=" "
                                    required
                                /></td>
                                <td><input
                                    type="text"
                                    value={newReceipt.receiptStatus}
                                    className="input"
                                    name="receiptStatus"
                                    onChange={handleChange}
                                    placeholder=""
                                    disabled
                                /></td>
                            </tr>
                            </tbody>
                        </table>
                        <div className="row-cols-1">
                            {uploading && (
                                <div style={{ marginBottom: "10px" }}>
                                    <p>  : {progress}%</p>
                                    <div
                                        style={{
                                            // width: "100%",
                                            backgroundColor: "#e0e0e0",
                                            borderRadius: "5px",
                                        }}
                                    >
                                        <div
                                            style={{
                                                width: `${progress}%`,
                                                backgroundColor: "#4caf50",
                                                height: "10px",
                                                borderRadius: "5px",
                                            }}
                                        ></div>
                                    </div>
                                </div>
                            )}
                            <div className="d-flex align-items-center gap-2 mb-2 my-2 justify-content-start">
                                <strong>  .jpg, .png  </strong>
                            </div>
                            <div className="d-flex gap-2 mb-3" style={{ width: '40%' }}>
                                <div className="input-group">
                                    <input
                                        type="file"
                                        className="form-control"
                                        accept=".jpg, .png"
                                        onChange={handleFileChange}
                                        placeholder=" "
                                        style={{ width: '70%' }}
                                    />
                                </div>
                            </div>
                        </div>
                        <button className="btn btn-primary me-4" type="submit"></button>
                        <button className="btn btn-info" type="button" onClick={() => setIsAdding(false)}></button>
                    </div>
                    </ul>
                )}
            </form>
        </div>
    );
};

export default ReceiptList;
